import React from 'react'
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom'

function GamaCompleta() {
    return (
        <div>
        <button type="button" class="btn btn-primary btn-sm"><Link to="/Servicios">Atrás</Link></button>
        <div class="row justify-content-md-center">
            <div class="col-sm-8">
                <div class="card">
                    <div class="card-body">
                        <h5 class="card-title">GAMA COMPLETA DE PRODUCTOS</h5>
                        <p class="card-text">Soluciones integrales de fijación para equipos técnicos de construcción de edificios y construcción de plantas.</p>
                        <li>Las abrazaderas de tubería para sistemas de tuberías ligeras a pesadas y los carriles de montaje con amplios accesorios forman la gama principal para las áreas de construcción sanitaria, de calefacción y ventilación, así como para la tecnología de aire acondicionado.</li>
                        <li>Una amplia gama de puntos fijos, elementos deslizantes, abrazaderas de tubo de refrigeración, tecnología de pasador y accesorios de montaje completan la gama completa de TGA</li>
                        <li>Con el sistema de servicio pesado, ofrecemos una solución modular para la construcción de tuberías pesadas, así como para la construcción industrial y de plantas.</li>
                        <li>La producción interna y un amplio almacén central son la mejor base para una disponibilidad óptima.</li>
                    </div>
                </div>
            </div>
        </div>
        </div>
    )
}
export default GamaCompleta
